import styled from "styled-components";
import { Text } from "../../assets/styles/component";

import { YouTube } from "../../assets/images/Socials/Youtube";
import { Odnoklassniki } from "../../assets/images/Socials/Odnoklassniki";
import { Vk } from "../../assets/images/Socials/Vk";
import { Telegram } from "../../assets/images/Socials/Telegram";
import { Instagram } from "../../assets/images/Socials/Instagram";
import { YandexZen } from "../../assets/images/Socials/YandexZen";
import MapImg from "../../assets/images/Elements/mapMobile.webp";

import { EngLoc } from "../../assets/constants/localizations";

const SGuest = styled.div`
  display: flex;
  flex-direction: column;
  padding: 3.75rem 1rem;

  @media (min-width: 600px) {
    padding: 3.75rem 4%;
  }

  gap: 1.563rem;

  & > p {
    font-weight: 400;
    font-size: 0.875rem;
    line-height: 150%;

    max-width: 19.75rem;

    margin-bottom: 1.875rem;
  }
`;

const Title = styled.h3`
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  font-weight: 400;
  font-size: 2.375rem;
  line-height: 110%;
`;

const Map = styled.div`
  display: flex;
  justify-content: center;
  align-items: flex-end;

  margin: 0 auto;
  width: 100%;
  max-width: 28.125rem;

  box-sizing: border-box;
  padding: 0.938rem 0.75rem;

  border-radius: 1.063rem;

  background-image: url(${MapImg});
  background-repeat: no-repeat;
  background-size: cover;
`;

const Contacts = styled.div`
  display: flex;
  flex-direction: column;
  box-sizing: border-box;

  gap: 1.25rem;

  width: 100%;
  max-width: 25rem;
  margin-top: 18.125rem;
  padding: 1.5rem;

  text-align: left;

  background: rgba(255, 255, 255, 0.21);
  border: 2px solid rgba(238, 247, 255, 0.22);
  backdrop-filter: blur(16.5px);
  border-radius: 1.063rem;

  & p,
  & a {
    color: #fff;
  }
`;

const Phone = styled.a`
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 150%;
`;

const Email = styled.a`
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 150%;
`;

const Socials = styled.div`
  display: flex;
  gap: 0.938rem;
`;

const Social = styled.a`
  cursor: pointer;

  & svg {
    width: 1.5rem;
    height: 1.5rem;
    aspect-ratio: 30 / 30;
  }
`;

const Guest = ({ language }) => {
  return (
    <SGuest>
      <Title>
        {language === "Ru" ? "Приходите в гости" : EngLoc.footer.title}
      </Title>

      <Text size={14} color="#3C3C3C">
        {language === "Ru"
          ? `Мы очень любим знакомиться в жизни: угостим вас очень вкусным кофе или
          чаем, накормим пироженными и познакомим с дружной командой SocialJet
          :)`
          : EngLoc.footer.desc_guest}
      </Text>

      <Map>
        <Contacts>
          <Phone href="tel:74957685865" aria-label={"Позвонить нам на телефон"}>
            +7 (495) 768 58 65
          </Phone>

          <Text size={20} color="#3C3C3C">
            {language === "Ru"
              ? "г. Москва,ул. Большая Садовая, дом 5, корпус 1, офис 515"
              : EngLoc.footer.adress}
          </Text>

          <Email
            href="mailto:partners@socialjet.pro"
            aria-label={"Написать нам на почту"}
          >
            partners@socialjet.pro
          </Email>

          <Socials>
            <Telegram />

            <Vk />

            <Social
              aria-label="Перейдите по ссылке, чтобы узнать о нас в соц сетях"
              href="https://t.me/socialjet"
              target="_blank"
            >
              <Instagram />
            </Social>

            <Social
              aria-label="Перейдите по ссылке, чтобы узнать о нас в соц сетях"
              href="https://vk.com/socialjet"
              target="_blank"
            >
              <Odnoklassniki />
            </Social>

              <YandexZen />

            <Social
              aria-label="Перейдите по ссылке, чтобы узнать о нас в соц сетях"
              href="https://zen.yandex.ru/id/6023a5d824c236744ae451e3"
              target="_blank"
            >
              <YouTube />
            </Social>
          </Socials>
        </Contacts>
      </Map>
    </SGuest>
  );
};

export default Guest;
