import styled from "styled-components";
import Guest from "./GuestMobile";
import NavFooter from "./NavFooterMobile";

const SFooter = styled.footer`
  display: flex;
  flex-direction: column;
`;

const Footer = ({language}) => {
  return (
    <SFooter id={"footer"}>
      {window.location.pathname != "/allcases" && <Guest language={language} />}
      <NavFooter language={language} />
    </SFooter>
  );
};

export default Footer;
