import styled from "styled-components";
import { Text } from "../../assets/styles/component";

import { YandexZen } from "../../assets/images/Socials/YandexZen";
import { Vk } from "../../assets/images/Socials/Vk";
import { Odnoklassniki } from "../../assets/images/Socials/Odnoklassniki";
import { Telegram } from "../../assets/images/Socials/Telegram";
import { Instagram } from "../../assets/images/Socials/Instagram";
import { YouTube } from "../../assets/images/Socials/Youtube";

import { EngLoc } from "../../assets/constants/localizations";

const SNavFooter = styled.div`
  display: flex;
  flex-direction: column;
  padding-top: 6.563rem;
  padding-bottom: 3.125rem;
  margin: 0 1rem;

  @media (min-width: 600px) {
    margin: 0 4%;
  }
`;

const Phone = styled.a`
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 150%;
  color: #3c3c3c;
  margin-top: 0.188rem;
`;

const Email = styled.a`
  margin-top: -0.938rem;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 150%;
  color: #1073d3;
`;

const NavInner = styled.div`
  display: grid;
  justify-content: flex-start;
  align-content: flex-start;

  gap: 1.875rem;
  grid-template-columns: repeat(2, minmax(calc(50% - 20px), 1fr));

  text-align: left;

  margin-bottom: 1.438rem;
`;

const NavMenu = styled.nav`
  display: flex;
  flex-direction: column;
  text-align: left;
  gap: 0.875rem;
`;

const NavItem = styled.a`
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 150%;
  cursor: pointer;
  color: #3c3c3c;
`;

const NavContacts = styled.div`
  text-align: left;
  max-width: 21.875rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1.563rem;
`;

const NavSocials = styled.div`
  display: flex;
  justify-content: flex-start;
  gap: 1.125rem;

  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
`;

const Social = styled.a`
  opacity: 0;
  cursor: pointer;
  & svg {
    width: 1.875rem;
    height: 1.875rem;
    min-width: 1.875rem;
    aspect-ratio: 20 / 20;
  }
`;

const Link = styled.a`
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 150%;
  text-decoration-line: underline;
  color: #000000;
  opacity: 0.4;

  margin-bottom: 1.063rem;
`;

const Logo = styled.svg`
  margin-top: -3.125rem;
  aspect-ratio: 179 / 86;

  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
`;

